.pDisplay {
    margin: 20px;
    transition: 0.2s all ease-in-out;
}

.cardDisplayer {
    cursor: pointer;
}

.productContainer {
    max-width: 276px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: relative;
}

.productContainer img{

}
.container{
    padding: 15px !important;
    display: flex;
    flex-direction: column;
}
.containerDiv{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.containerDiv img{
    width: 100%;
    margin: 0;
    max-height: 173px;
}

.buttonDiv{
    position: relative;
}

.containerDiv p{
 text-align: center;
 color: black;
 font-size: 18px;
 cursor: pointer;
}

.containerDiv p:hover{
    color:rgb(247, 169, 7)
}

.containerDiv article{
    text-align: center;
    color: black;
}
.btnIcon{
    position: absolute;
    top: 50%;
    left: 42%;
    font-size: 19px;
    z-index: 1000;
    background-color: #E6393B;
    color: white;
    border: none;
    outline: none;
    padding: 8px 10px;
    border-radius: 7px;
}
.btnIcon:hover{
    background-color: #16425b;
}
.btnGroupCart{
    margin-top: 20px;
    border: none;
}
.btnGroupCart button{
    padding: 10px;
}
.cartBtn{
    background-color: #222222;
    border: none;
    cursor: pointer;
}
.iconBtn{
    border: none;
    background-color: #E6393B !important;
}
.btnGroupCart:hover .cartBtn{
    background-color: #16425b;
}
.outOfStock{
    position: absolute;
    bottom: 0;
}
.revealDiv{
    position: relative;
}
.overlayDiv{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.2);
}
.outOfStock{
    bottom: 0;
    color: white;
    background-color: #E6393B;
    width: 100%;
    text-align: center;
    padding: 10px 0px;
    position: absolute;
}